.menu {
    &_aside {
        flex: 1 1 auto;
        margin: 0;
        padding: 0;
    }
    &_item {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        
        
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;
        letter-spacing: -0.01em;

        color: $menu_color-main;
        text-decoration: none;
        
        padding: 11px 8px 11px 11px;
        margin-bottom: 18px;
        border-radius: 8px;
        
        @media (max-width: 1000px) {
             padding: 11px 8px;
        }
        
        &:hover,
        &.active {
            transition: color ease 0.4s, background ease 0.4s;
            background: $purple;
            color: $white;
        }
        &_icon {
            font-size: 24px;
            padding-right: 10px;
            
        }
        &_text {
            font-size: 14px;
            line-height: 21px;
            flex: 1 1 auto;
            @media (max-width: 1000px) {
                display: none;
            }   
        }
        &_subMenu {
            @media (max-width: 1000px) {
                display: none;
            }
        }
    }
}