.user {
    &_block {
        letter-spacing: 0.01em;
        text-decoration: none;
        &_avatar {
            width: 42px;
            height: 42px;
            margin-right: 12px;
        }
        &_info {
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 18px;

            letter-spacing: 0.01em;

            color: $dark-gray;
            @media (max-width: 1000px) {
                display: none;
            }

        } 
        &_name {
            display: block;
            
            font-weight: 500;
            font-size: 14px;
            line-height: 21px;

            color: $black;
        }
    }
}