.content {
    flex: 1 1 auto;
    padding: 40px 28px;
    
    overflow-x: hidden;
    overflow-y: auto;
    @media (max-width: 710px) {
        padding: 20px;
    }
    @media (max-width: 510px) {
        padding: 20px 10px;
    }
    
    &_block {
        max-width: 968px;
        margin: 0 auto 50px;
        border-radius: 30px;
        
        &-white {
            background: $white;
            box-shadow: $content-boxShadow;
        }
        &__padding {
            padding: 0px 38px 30px 38px;
        }
        &-header {
            align-items: center;
            justify-content: space-between;
            padding: 30px 38px;
            @media (max-width: 710px) {
                padding: 20px;
            }
            @media (max-width: 540px) {
                flex-direction: column;
                justify-content: flex-start;
                align-items: flex-start;
            }
            &-h1 {
                font-style: normal;
                font-weight: 600;
                font-size: 22px;
                line-height: 33px;

                letter-spacing: -0.01em;

                color: $black;
            }
            &-subHeader {
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 21px;

                letter-spacing: -0.01em;

                color: $green;
            }
        }
        &_main {
            
        }
        
    }
}
.btn {
    display: inline-block;
    padding: 4px 12px;
    border: 1px solid $grey;
    background: $alt-grey;
    border-radius: 4px;
    text-decoration: none;
    color: $white;
    &_active {
        background: $btn-bg-active;
        border: 1px solid $btn-border-active;
        border-radius: 4px;
        color: $btn-text-active;
        padding: 4px 18px;
    }
    &_inactive {
        background: $btn-bg-inactive;
        border: 1px solid $btn-main-inactive;
        border-radius: 4px;
        color: $btn-main-inactive;
        
    }
}