.table { 
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;

    letter-spacing: -0.01em;

    color: $table-color;
    
    padding-bottom: 30px;
    
    
    &_row {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 0px; 
        margin: 0 38px;
        border-bottom: 1px solid #EEEEEE;
        
    }
    &_col {
        min-width: 16.6%;
        padding: 20px 5px 20px 0;
        
        overflow-wrap: anywhere;
    }
    &_header {
        color: $alt-grey;
        margin: 0;
        padding: 0 38px;
    }
    &_manager {
        .table {
            &_col {
                &:nth-child(1) {
                    min-width: 17%;
                    width: 17%;
                }
                &:nth-child(2) {
                    min-width: 13%;
                    width: 13%;
                }
                &:nth-child(3) {
                    min-width: 16%;
                    width: 16%;
                }
                &:nth-child(4) {
                    min-width: 21%;
                    width: 21%;
                }
                &:nth-child(5) {
                    min-width: 14%;
                    width: 14%;
                }
                &:nth-child(6) {
                    flex: 1 1 auto;
                    text-align: center;
                }
            }
        }
        @media (max-width: 710px) {
            &-one {
                .table {
                    &_header {
                        display: none;
                    }
                    &_col {
                        &:nth-child(1) {
                            &:before {
                                content: 'Customer Name';
                            }
                        }
                        &:nth-child(2) {
                            &:before {
                                content: 'Company';
                            }
                        }
                        &:nth-child(3) {
                            &:before {
                                content: 'Phone Number';
                            }
                        }
                        &:nth-child(4) {
                            &:before {
                                content: 'Email';
                            }
                        }
                        &:nth-child(5) {
                            &:before {
                                content: 'Country';
                            }
                        }
                        &:nth-child(6) {
                            &:before {
                                content: 'Status';
                            }
                        }
                        &:nth-child(1),
                        &:nth-child(2),
                        &:nth-child(3),
                        &:nth-child(4),
                        &:nth-child(5),
                        &:nth-child(6) {
                            width: 33.3%;
                            min-width: 30%;
                            padding: 5px;

                            text-align: left;
                            @media (max-width:651px) {
                                width: 50%;
                                min-width: 50%;
                            }
                            @media (max-width:450px) {
                                width: 100%;
                                min-width: 100%;
                            }
                            &:before {
                                display: block;
                                color: $alt-grey;
                                padding: 0;
                                margin: 0;
                                font-size: 12px;
                            }
                        }
                    }
                    &_row {
                        flex-wrap: wrap;
                        padding-bottom: 5px;
                        margin: 0 20px;
                        & + & {
                            padding-top: 5px
                        }
                    }
                }
                
            }
            
        }
        &-two {
            display: block;
            overflow-x: auto;
            white-space: nowrap;
            width: 100%;
        }
    }
    &-data {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;

        letter-spacing: -0.01em;

        color: $alt-grey;
        
        justify-content: space-between;
        
        @media (max-width: 710px) {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .pager {
                padding-top: 15px;
            }
        }
    }
}