.pager {
    justify-content: space-between;
    align-items: center;
    &-item {
        background: $pager-bg;
        border: 1px solid $pager-border;
        border-radius: 4px;
        
        text-decoration: none;
        
        width: 25px;
        height: 25px;
        transition: background ease 0.4s, border ease 0.4s;
        
        color: $pager-text;
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        letter-spacing: -0.01em;
        
        display: inline-flex;
        justify-content: center;
        align-items: center;
        &:first-child,
        &:last-child {
            font-size: 8px;
            line-height: 15px;
        }
        & + & {
            margin-left: 12px;
        }
        &:hover,
        &__active {
            background: $pager-bg-active;
            border: 1px solid $pager-bg-active;
            color: $white;
        }
        &__clear {
            background: none;
            border: none;
            color: $pager-text;
            &:hover {
                background: none;
                border: none;
                color: $pager-text;
            }
        }
    }
}