.aside {
    min-width: 306px;
    width: 306px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    
    padding: 28px;
    
    background: #FFFFFF;
    box-shadow: 0px 10px 60px rgba(226, 236, 249, 0.5);
    @media (max-width: 1000px) {
        &:not(.aside.open) {
            min-width: 50px;
            width: 50px;
            padding: 28px 5px;
            transition: ease all 0.4s;
            z-index: 2;
            & + .content,
            .icon-right:before {
                transition: ease all 0.4s;
            }
        }
        &.open {
            transition: ease all 0.4s;
            z-index: 2;
            & + .content {
                margin-left: -256px;
                transition: ease all 0.4s;
            }
            .aside_btn:before {
                transition: ease all 0.4s;
                transform: rotate(-180deg);
                display: inline-block;
            }
            .user_block_info,
            .logo_text {
                white-space: nowrap;
            }
            .user_block_info,
            .logo_text,
            .menu_item_subMenu,
            .menu_item_text {
                display: block;
            }
        }
            
        
//        &.open {
//            min-width: 306px;
//            width: 306px;
//        }
        
    }
    &_btn {
        display: none;
        @media (max-width: 1000px) {
            display: block;
            margin-bottom: 15px;
            text-align: right;
            text-decoration: none;
            color: $grey;
            padding-right: 12px;
            
        }
    }
    
}