*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
    background: $base-color;
	min-height: 100%;
    height: 100%;
	position: relative;
    font-family: 'Poppins';
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?l9jw21');
  src:  url('fonts/icomoon.eot?l9jw21#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?l9jw21') format('truetype'),
    url('fonts/icomoon.woff?l9jw21') format('woff'),
    url('fonts/icomoon.svg?l9jw21#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-logo:before {
  content: "\e900";
}
.icon-key:before {
  content: "\e901";
}
.icon-d:before {
  content: "\e902";
}
.icon-user:before {
  content: "\e903";
}
.icon-wallet:before {
  content: "\e904";
}
.icon-discount:before {
  content: "\e905";
}
.icon-message:before {
  content: "\e906";
}
.icon-search:before {
  content: "\e907";
}
.icon-right:before {
  content: "\e908";
}
.icon-left_pag:before {
  content: "\e909";
}
.icon-right_pag:before {
  content: "\e90a";
}
h1 {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
}
