.search {
    background: $light-grey;
    border-radius: 10px;
    position: relative;
    @media (max-width: 540px) {
        width: 100%;
        margin-top: 15px;
    }
    &_input {
        border: none;
        background: none;
        outline: none; // люблю так
        
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
        letter-spacing: -0.01em;
        
        padding: 10px 10px 10px 40px;
        
        color: $black;
        
        &::placeholder { 
          color: $alt-grey;
        }
        @media (max-width: 540px) {
            width: 100%;
        }
    }
    &_btn {
        border: none;
        background: none;
        outline: none;
        font-size: 24px;
        color: $searchIcon;
        
        cursor: pointer;
        z-index: 1;
        
        position: absolute;
        top: 6px;
        left: 4px;
    }
}