
.logo {
    
    text-decoration: none;
    align-items: center;
    line-height: 39px;
    letter-spacing: -0.01em;
    font-style: normal;
    color: $grey;
    
    &_icon {
        font-size: 37px; 
        color: #000;
            padding-right: 15px;
    }
    &_text {
        font-weight: 600;
        font-size: 26px;
        color: $black;
        @media (max-width: 1000px) {
            display: none;
        }
    }
    &_version {
        font-weight: 500;
        font-size: 10px;
        line-height: 15px;
        color: $grey;
    }
}
